import { Injectable } from '@angular/core';
import{movieList} from '../model/movielist';
import {Movie} from '../model/movie';
import{HttpClient , HttpHeaders, HttpParams} from '@angular/common/http';
// asyn with observable
import {of} from 'rxjs/internal/observable/of';
import { Observable } from 'rxjs/internal/Observable';
import{MessageService} from './message.service';
import { AuthenticationService } from 'src/app/services/auth.service';

const httpOption = {
  headers : new HttpHeaders({'Content-Type':'application/json'})
};
@Injectable({
  providedIn: 'root'
})

export class MovieService {
  private  moviesUrl = 'http://localhost:8080/abc/api/movies';

  createHeader(): HttpHeaders{
    return new HttpHeaders({
      'Authorization': 'Basic ' +
        btoa('admin1:admin')
    }); 
  }
  private headers = new HttpHeaders({
    'Authorization': 'Basic ' +
      btoa('admin1:admin')
  }); 
  getMovie(): Observable<Movie[]>{
        this.messageService.add(`${new Date().toLocaleTimeString()}. Get movie list.`);
        return of(movieList);
  }

  getMovieFromId(id: number): Observable<Movie>{
   
    return of(movieList.find(movie => movie.id === id));
}

  getMovie2(): Observable<Movie[]>{
    const params = new HttpParams()
    .set('tenantId', 'tenantId1');

    return this.http.get<Movie[]>(this.moviesUrl,{params,headers:this.headers});
}
getMovieFromId2(id: number): Observable<Movie>{
  const params = new HttpParams()
    .set('tenantId', 'tenantId1');
  const url = `${this.moviesUrl}/${id}`;
  return this.http.get<Movie>(url,{params,headers:this.headers});
}

updateMovie(movie : Movie) : Observable<any>{
  
  return this.http.put(`${this.moviesUrl}/${movie.id}`,movie, httpOption);
}

  constructor(
    public messageService : MessageService,
    private http : HttpClient,
    private authenticationService: AuthenticationService
  ) { }

}
